import React, { Component } from 'react'

const formGroupStyle = {
    display: 'flex',
    flexFlow: 'column nowrap',
    alignItems:'flex-start'
};


export default class DinamicFormField extends Component {

    render() {
        
        if(this.props.funcao === 'Student'){
            return (
                <React.Fragment>
                    <div style={formGroupStyle} className="form-group col-md-auto">
                        <label >Numero de Matrcula</label>
                        <input onChange={this.props.handleChangeMatricule} type="text" className="form-control" id="inputMat" placeholder="N Matrcula"/>
                    </div>
                </React.Fragment>
            )
        }
        else if(this.props.funcao === 'Professor'){
            return(
                <React.Fragment>
                    <div style={formGroupStyle} className="form-group col-md-auto">
                        <label >Titulao</label>
                        <input onChange={this.props.handleChangeTittle} type="text" className="form-control" id="inputTit" placeholder="Titulao"/>
                    </div>
                    <div style={formGroupStyle} className="form-group col-md-auto">
                        <label >rea de Pesquisa</label>
                        <input onChange={this.props.handleChangeSearchArea}  type="text" className="form-control" id="inputAreaPesq" placeholder="rea de Pesquisa"/>
                    </div>
                </React.Fragment>
            )
        }
        else{
            return (<React.Fragment></React.Fragment>)
        }
    }
}
